@font-face {
    src:
    url('Inter-Black.otf') format('otf'),
    url('Inter-Black.ttf') format('ttf'),
    url('Inter-Black.woff') format('woff'),
    url('Inter-Black.woff2') format('woff2');
    font-family: 'Inter';
    font-weight: 900;
}

